.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.body-background {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ebe9f9+0,ebe9f9+44,c1bfea+100 */
  background: #ebe9f9; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #ebe9f9 0%,#ebe9f9 44%,#c1bfea 100%); /* Chrome10-25,Safari5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#ebe9f9),color-stop(44%, #ebe9f9),to(#c1bfea));
  background: -o-linear-gradient(left, #ebe9f9 0%,#ebe9f9 44%,#c1bfea 100%);
  background: linear-gradient(to right, #ebe9f9 0%,#ebe9f9 44%,#c1bfea 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ebe9f9', endColorstr='#c1bfea',GradientType=1 ); /* IE6-9 */
}

.title {
  font-family: 'Cormorant Upright', serif;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



